<template>
<div class="shilde">
    <div>
        <h3>功能分配</h3>
        <h5>为角色<span style="color:#ff0000"><span v-if="checkData[0]">{{checkData[0].rolename}}</span></span>分配功能</h5>
        <div>
            <table class="table table-bordered table-striped table-hover">
                <thead>
                    <tr>
                        <th><input type="checkbox" name="" id=""></th>
                        <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(datas,index) in page.alldatas" :key="index">
                        <td v-for=" (data,index) in datas" :key="index" v-if="index!='createok'">
                            <input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" v-model="datas.createok" v-if="index=='id'">
                            <span v-else v-html="data"></span>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div>
                <button class="btn btn-info" @click="updatejueFun">提交</button> <button class="btn btn-warning" @click="closeupdateFun">返回</button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data: () => {
        return {
            page: {
                alldatas: [],
                haddatas: [],
                titleData: ["功能名", "类型", "描述"]
            },
            data: {
                roleid: "",
                addData: [],
                deleteData: []
            }
        };
    },
    props: {
        tablefunall: Boolean,
        state: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        },
    },
    watch: {
        tablefunall(news) {
            if (news) {
                this.loginFunData();
            }
        }
    },
    methods: {
        //生成选中状态
        createok() {
            var vm = this;
            var index = 0;
            var alldatas = vm.page.alldatas;
            var haddatas = vm.page.haddatas;
            if (alldatas instanceof Array) {
                alldatas.forEach(function (datas) {
                    if (haddatas instanceof Array) {
                        haddatas.forEach(function (data) {
                            if (datas.id == data) {
                                alldatas[index].createok = true;
                            }
                        })
                    }
                    index += 1;
                })
            }
        },
        //修改功能提交
        updatejueFun() {
            this.data.roleid = this.checkData[0].id;
            this.$api.http.opreateHttp(
                this,
                "updataDistributionFunction.action",
                this.data,
                "修改功能"
            );
        },
        //功能分配界面隐藏
        closeupdateFun() {
            var vm = this;
            vm.$api.opreate.emitCall("update:tablefunall", false, vm);
        },
        // 获取功能
        loginFunData() {
            var vm = this;
            vm.$axios
                .post(vm.HOST + "selecDistributionFunction.action", {
                    id: this.checkData[0].id,
                    state: this.state
                })
                .then(function (success) {
                    let data = success.data;
                    if (success.bool) {
                        vm.page.alldatas = data.allFunction;
                        vm.page.haddatas = data.hadFunction;
                        vm.createok();
                    } else{
                        vm.$layer.msg("功能获取失败", {
                            icon: 5
                        });
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        //修改选中状态
        updateCheckData(data, e) {
            var vm = this;
            var data = data;
            var checkdata = vm.data;
            var checkad = e.target.checked;
            data.id += "";
            if (checkad) {
                if (!checkdata.addData.indexOf(data.id) > -1) {
                    if (checkdata.deleteData.indexOf(data.id) > -1) {
                        checkdata.deleteData = checkdata.deleteData.splice(data.id, 1);
                    }
                    if (!(vm.page.haddatas.indexOf(data.id) > -1)) {
                        checkdata.addData.push(data.id);
                    }
                }
            } else {
                if (!checkdata.deleteData.indexOf(data.id) > -1) {
                    if (checkdata.addData.indexOf(data.id) > -1) {
                        checkdata.addData = checkdata.addData.splice(data.id, 1);
                    }
                    if (vm.page.haddatas.indexOf(data.id) > -1) {
                        checkdata.deleteData.push(data.id);
                    }
                }
            }
        }
    }
};
</script>

<style>
.shilde {
    position: absolute;
    top: 0;
    left: -1rem;
    padding: 2rem;
    width: 100%;
    height: 100%;
    background: #fff;
    z-index: 1000;
}
</style>



