<template>
    <div>
         <div class="col-sm-12">
            <div class="col-sm-2">
                <h5 ><span v-text="statetitle"></span>{{opreatetitle}}</h5>
            </div>
            <div class="col-sm-12">
                <form class="form form-horizontal col-sm-8" name="myform">
                  <!-- 特有输入栏 菜单类别 -->
                   <div v-if="stateId=='gongnengguanli'"  class="form-group">
                      <!-- 选择菜单级别 -->
                      <label for="" class="col-sm-2 control-label">类别</label>
                        <div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type1" value="0" v-model="addData.type" :disabled="opreate=='update'"><label for="type1" class="control-label">目录</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type2" value="1" :checked="true" v-model="addData.type" :disabled="opreate=='update'"><label for="type2"  class="control-label">菜单</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type3" value="2" v-model="addData.type" :disabled="opreate=='update'"><label for="type3"  class="control-label">按钮</label> 
                          </div>
                        </div>
                   </div>
                   <!-- 公共输入栏 -->
                    <div class="form-group" >
                        <label class="control-label col-sm-2"><span v-text="typename[addData.type]"></span>名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入'+typename[addData.type]+'名,最多为20位'" v-model="addData.name" :data-vv-as="typename[addData.type]+'名'"
                             v-validate="$api.opreate.validateAdd('name',20,stateId,opreate)">
                            </div>
                             <div class="text-danger">
                                 <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!-- 特有输入栏 -->
                    <div v-if="stateId=='gongnengguanli'">
                      <!--菜单url-->
                      <div class="form-group" v-if="addData.type==1">
                        <label class="control-label col-sm-2">菜单url</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('url')&&addData.url!='','has-error':errors.has('url')}">
                            <input type="text" name="url" class="form-control" placeholder="请输入菜单url,只能输入英文和数字" v-model="addData.url" 
                              data-vv-as="url"   v-validate="$api.opreate.validateAdd('url',20,stateId,opreate)"></div>
                             <div  class="text-danger">
                            <span v-show="errors.has('url')">{{errors.first("url")}}</span>
                        </div>
                      </div>
                      <!-- 应用系统 -->
                      <div class="form-group">
                         <label class="control-label col-sm-2">应用系统</label>
                        <div class="col-sm-6" >
                            <select type="text" name="url" class="form-control"  v-model="addData.application"><option :value="item.application_system_id" v-for="(item,index) of applications" :key="index" v-text="item.system_name"></option> </select></div>
                      </div>
                      <!-- 上级菜单 -->
                      <div class="form-group" v-if="addData.type!=0">
                          <label for="" class="col-sm-2 control-label">上级菜单</label>
                          <div class="col-sm-6">
                            <input type="text" class="form-control"  v-model="addData.parentName" @click="loginmenuData" readonly="readonly">
                          </div>
                      </div>
                        <!-- 授权标识 -->
                      <div class="form-group" v-if="addData.type!=0" >
                          <label for="" class="col-sm-2 control-label">授权标识</label>
                          <div class="col-sm-6" :class="{'has-success':!errors.has('perms')&&addData.perms!='','has-error':errors.has('perms')}">
                            <input type="text" class="form-control" placeholder="用多个逗号分隔；如user:list,user:delete" v-model="addData.perms" name="perms" data-vv-as="授权标识" v-validate="$api.opreate.validateAdd('perms',0,stateId,opreate)">
                          </div>
                          <div class="text-danger">
                            <span v-show="errors.has('perms')">{{errors.first("perms")}}</span>
                          </div>
                      </div>
                        <!-- 排序号 -->
                      <div>
                          <div class="form-group">
                          <label for="" class="col-sm-2 control-label">排序号</label>
                          <div class="col-sm-6" :class="{'has-success':!errors.has('order_num')&&addData.order_num!='','has-error':errors.has('order_num')}">
                            <input type="number" class="form-control" v-model="addData.order_num" data-vv-as="排序号" v-validate="$api.opreate.validateAdd('order_num',11,stateId,opreate)" name="order_num">
                          </div>
                          <div class="text-danger">
                            <span v-show="errors.has('order_num')">{{errors.first("order_num")}}</span>
                          </div>
                      </div>
                        <!-- 图标 -->
                      <div class="form-group">
                          <label for="" class="col-sm-2 control-label">图标</label>
                          <div class="col-sm-6">
                            <input type="text" class="form-control" placeholder="菜单图标" name="icon" v-model="addData.icon">
                          </div>
                      </div>
                        </div>
                    </div>
                     <!-- 公共输入栏 描述 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">详情描述</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="请输入描述信息，最多不超过50个字符" v-model="addData.detail" data-vv-as="描述"
                            v-validate="'required|cnlength:50'"></textarea>
                            </div>
                         <div class="text-danger">
                          <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <!-- 按钮 -->
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button> 
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddclick">返回</button> 
                    </div>
                </form>
            </div>
            <div id="menuLayer" style="display:none">
                <ul id="menuTree" class="ztree"></ul>
            </div>
        </div>
    </div>
</template>
<script>
import ztree from "ztree";
import "ztree/css/metroStyle/metroStyle.css";
import "ztree/js/jquery.ztree.all.min.js";
export default {
  data: function() {
    return {
      initData: {
        id: "",
        name: "",
        url: "",
        detail: "",
        type: "1",
        order_num: 0,
        perms: "",
        parentName: "一级菜单",
        parentId: 0,
        application: "",
        icon:""
      },
      typename:["目录","菜单","按钮"],
      addData: {},
      nameVer: "",
      urlVer: "",
      ztree: {},
      opreatetitle: "添加",
      applications: [],
      menuData: [],
      menushow: false
    };
  },
  props: {
    stateId: String,
    statetitle:String,
    opreate: String,
    tableSelectData: Object,
    inforadmin:Boolean
  },
  mounted: function() {
    this.loginApplicationData();
    this.logincheckData();
  },
  filters: {},
  watch: {
    checkData(newvalue) {
      this.logincheckData();
    },
    opreate(news) {
      this.logincheckData();
    }
  },
  computed: {
    checkData() {
      return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
    }
  },
  methods: {
    //获取菜单数组
    loginmenuData() {
      var vm = this;
      if (vm.addData.application !== "") {
        vm.$axios
          .post(vm.HOST + "selectMenu.action", { id: vm.addData.application })
          .then(success => {
            if (success.bool) {
              vm.menuData = success.data;
              vm.menuData.push({"oder_num":0,"id":0,"parentId":0,"pagename":"根目录"});
              vm.getMenu();
              vm.menuTree();
            } else {
            }
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        vm.$layer.alert("请先选择应用系统", { icon: 0, time: 1000 });
      }
    },
    //获取应用系统
    loginApplicationData() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "selectApplicationId.action")
        .then(success => {
          if (success.bool) {
            vm.applications = success.data;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //根据按钮初始化绑定数据
    logincheckData() {
      var data;
      if (this.opreate == "update") {
        if (this.checkData.length > 0) {
          data = this.$api.opreate.ObjectCopy(this.checkData);
          this.addData = this.cutStr("detail", "name", data[0]);
          this.opreatetitle = "修改";
        }
      } else if (this.opreate == "add") {
        this.addData = this.$api.opreate.ObjectCopy(this.initData);
        this.opreatetitle = "添加";
      }
    },
    //格式化部分对象键名
    cutStr(name1, name2, Obj) {
      var obj = Obj;
      for (var key in obj) {
        if (key.indexOf(name1) > -1 && key != name1) {
          obj[name1] = obj[key];
          delete obj[key];
        }
        if (key.indexOf(name2) > -1 && key != name2) {
          obj[name2] = obj[key];  
          delete obj[key];
        }
      }
      return obj;
    },
    
    //返回按钮按下
    inforAddclick() {
      this.addData = this.$api.opreate.ObjectCopy(this.initData);
      this.$validator.errors.clear();
       this.$emit('update:inforadmin', false);
    },
    //数据添加操作
    addDatas() {
      var vm = this;
      var stateId = vm.stateId;
      var data = vm.addData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      // 修改操作
      var bool;
      if (vm.opreate == "update") {
        if (vm.stateId == "gongnengguanli") {
          var successcall=vm.$api.http.loginleftmenu;
          bool=vm.$api.http.opreateHttp(vm, "updatePageInf.action", datas, "修改",successcall)
        } else if (vm.stateId == "jueseguanli") {
          bool=vm.$api.http.opreateHttp(vm, "updateRole.action", datas, "修改","");
        }
      } else if (vm.opreate == "add") {
        //添加操作
        if (vm.stateId == "gongnengguanli") {
          var successcall=vm.$api.http.loginleftmenu;
          bool=vm.$api.http.opreateHttp(vm, "addFunction.action", datas, "添加",successcall);
        } else if (vm.stateId == "jueseguanli") {
         bool= vm.$api.http.opreateHttp(vm, "addRole.action", datas, "添加","");
        }
      }
      setTimeout(function(){
        vm.$validator.errors.clear();
      },300)
      bool.then(function(success){
        if(success){
          vm.logincheckData();
          setTimeout(function(){
            vm.inforAddclick();
          },800);
        }else{
         vm.$layer.msg("请消除输入错误后提交", {
            icon: 5
          });
        }
      })
      
    },
    //生成选择菜单类别树
    menuTree() {
      var vm = this;
      layer.open({
        type: 1,
        offset: "50px",
        skin: "layui-layer-molv",
        title: "选择菜单",
        area: ["300px", "450px"],
        shade: 0,
        shadeClose: false,
        content: jQuery("#menuLayer"),
        btn: ["确定", "取消"],
        btn1: function(index) {
          var node = vm.ztree.getSelectedNodes();
          //选择上级菜单
          vm.addData.parentName = node[0].pagename;
          vm.addData.parentId = node[0].id;
          layer.close(index);
          document.getElementById("menuLayer").style.display = "none";
        },
        btn2: function(index) {
          layer.close(index);
          document.getElementById("menuLayer").style.display = "none";
        }
      });
    },
    //初始化菜单
    getMenu(menuId) {
      var setting = {
        data: {
          simpleData: {
            enable: true,
            idKey: "id",
            pIdKey: "parentId",
            rootPId: -1
          },
          key: {
            name: "pagename"
          }
        }
      };
      //加载菜单树
      //设置ztree的数据
      var vm = this;
      vm.ztree = $.fn.zTree.init($("#menuTree"), setting, vm.menuData);
      //编辑（update）时，打开tree，自动高亮选择的条目
      var node = vm.ztree.getNodeByParam("id", vm.addData.parentId);
      //选中tree菜单中的对应节点
      vm.ztree.selectNode(node);
      //编辑（update）时，根据当前的选中节点，为编辑表单的“上级菜单”回填值
      vm.addData.parentName = node.name;
    }
  }
};
</script>






