<template>
  <div class="panel panel-info">
    <div class="panel-body">
      <div v-show="page.titleData.length>0" style="display:none">
        <table class="table table-bordered table-striped table-hover">
          <thead>
            <tr>
              <th><input type="checkbox" name="" id=""></th>
              <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
            </tr>
          </thead>
          <tbody v-show="page.datas.length>0">
            <tr v-for="(datas,index) in page.datas" :key="index">
              <td><input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" v-model="page.check[index]"></td>
              <td v-for="(data,index) in datas" :key="index" v-html="judgeData(data,index)" v-if="filtersname(index)">
              </td>
            </tr>
          </tbody>
        </table>
         <div v-show="page.datas.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error" @click=" loginclick">
           未查找到数据，点击请重试
          </div>
        <!-- 页码 -->
        <ul class="pagination col-sm-12" v-show="page.datas.length>0">
          <div class="col-sm-3">
            <a class="btn disabled" v-html="'位于第'+page.active+'页'"></a>
            <a class="btn disabled" v-html="'共'+page.pageTotal+'页'"></a>
          </div>
          <li @click="pageClick(pages)" v-for="pages in page.pages" :key="pages" :class="{'active':page.active==pages}">
            <!--class="{{addClass(pages)}} :class="{true:'active'}[page.active==pages]""-->
            <a href="javascript:void(0);" v-text="pages"></a>
          </li>
          <div class="col-sm-4">
            <div class="col-sm-4">
              <select class="form-control" v-model="page.pagelist" @change="countclick">
                <option v-for="(item,index) in page.pageselect" :key="index" :value="item" v-text="item" :selected="item==page.count"></option>
              </select>
            </div>
            <div class="col-sm-3 loginpage">
              <input type="text" class="form-control" v-model="loginpage" placeholder="前往页数" style="font-size:12px">
            </div>
            <div class="col-sm-2">
              <button class="btn btn-info" @click="pageClick(loginpage)">前往</button>
            </div>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      check: {},
      loginpage: "",
      inputinfor: false
    };
  },
  props: {
    tableSelectData: Object
  },
  mounted: function() {
    var vm = this;
    vm.loginclick();
  },
  computed: {
    page() {
      return this.$store.getters.GETUSERTABLEPAGE;
    }
  },
  methods: {
    //接收父级屏蔽参数
    filtersname(index){
      var filtername=this.tableSelectData.filterName;
      if(filtername instanceof Array){
        if(filtername.length>0){
          var bool=0;
          filtername.forEach(element => {
            if(index==element){
              bool+=1;
            }
        }); 
        if(bool>0){
          return false;
        }
        }
      }
      return true;
    },
    /**
     * filtername　数组
     */
    judgeData(value,index) {
      var vm = this;
      var key=index;
        switch (key) {
          case "regtime":
            return vm.$api.opreate.datafilter(value, "yyyyMMdd");
            break;
          case "state":
            if (Number(value) == 0) {
              return "<div class='label label-primary'>注销</div>";
            } else if (Number(value) == 1) {
              return "<div class='label label-primary'>正常</div>";
            } else if (Number(value) == 2) {
              return "<div class='label label-warning'>冻结</div>";
            }
            break;
          case "role_state":
            if (Number(value) == 0) {
              return "<div class='label label-primary'>未审核</div>";
            } else if (Number(value) == 1) {
              return "<div class='label label-success'>已审核</div>";
            } else if (Number(value) == 2) {
              return "<div class='label label-primary'>冻结</div>";
            }
            break;
          case "type":
            if (Number(value) == 0) {
              return "<div class='label label-primary'>目录</div>";
            } else if (Number(value) == 1) {
              return "<div class='label label-success'>菜单</div>";
            } else if (Number(value) == 2) {
              return "<div class='label label-info'>按钮</div>";
            }
            break;
            default :break;
        }
      return value;
    },
    //选择每页显示多少行数据函数
    countclick() {
      this.allcheck = false;
      this.$store.commit("UPDATEPAGEACTIVE", 1);
      this.loginclick();
    },
    //页面请求函数
    loginclick() {
      var vm = this;
      var tableSelectData = vm.tableSelectData;
      vm.$api.http.loginTableData(
        vm,
        tableSelectData.selectTableurl,
        tableSelectData.pageloginData
      );
      console.log(vm.page)
    },
    //页面点击函数
    pageClick(page) {
      var Active = page; //将要前往页面
      var active = this.page.active; //当前页面
      var total = this.page.pageTotal; //总页面
      if (Active == active) {
        this.loginpage = "";
        return;
      } else if (Active > total) {
        alert("没有第" + Active + "页");
        this.loginpage = "";
        return;
      } else if (Active == "首页") {
        Active = 1;
      } else if (Active == "尾页") {
        Active = total;
      } else if (Active == "«") {
        if (active == 1) {
          return;
        }
        Active = active - 1;
      } else if (Active == "»") {
        if (active == total) {
          return;
        }
        Active = active + 1;
      } else if (Active < 1) {
        Active = 1;
      }
      this.loginpage = "";
      if (this.loginclick && typeof this.loginclick === "function") {
        this.$store.commit("UPDATEPAGEACTIVE", Active);
        this.loginclick();
      }
    },
    //修改信息存入
    updateCheckData(data, e) {
      var vm = this;
      var check = e.target.checked;
      var checkData = vm.page.checkData;
      var index = checkData.indexOf(data);
      if (check) {
        checkData.push(data);
      } else {
        checkData.splice(index, 1);
      }
      vm.$store.commit("UPDATECHECKDATA", checkData);
      vm.$api.opreate.emitCall("tableCheck", vm.page.checkData, vm);
    }
  },
  watch: {
    pageloginData: function(news, olds) {
      this.loginclick();
      this.inputinfor = false;
      this.page.checkData = [];
      this.checkEmpty();
    }
  }
};
</script>

<style scoped>
.logindata-error{
  font-size: 18px;
  color: rgb(243, 29, 29);
}
.panel{
  border:none;
}
</style>



